>\n    </div>\n    <div class=\"data-table\">\n        <div class=\"table-header\">\n            <span v-for=\"header in tableHeaders\" :class=\"'table-header-text header-' + header\"> {{\n                    localization.get(header)\n            }} </span>\n        </div>\n        <div class=\"table-body\">\n            <div class=\"table-row\" :style=\"'background-color:' + (index % 2 == 0 ? 'white' : '#F5F5FA')\"\n                v-for=\"(row, index) in tableRows\">\n                <div class=\"row-parameter\">{{ row.parameter }}</div>\n\n                <div v-if=\"!props.editable\" class=\"row-data-type data-type\">{{ row.dataType.value }}</div>\n                <div v-else class=\"row-data-type data-type-editable\">\n                    <select class=\"data-type-combo\" v-model=\"row.dataType.value\" @change=\"handleDataTypeChange(row)\">\n                        <option v-for=\"dataType in dataTypes\" :selected=\"dataType == row.dataType.value\">{{ dataType }}\n                        </option>\n                    </select>\n                </div>\n\n                <div v-if=\"!props.editable\" class=\"row-current-value current-value\">\n                    {{ screens.find((screen) => screen.ID == row.currentValue)?.name || row.currentValue || \"-\" }}\n                </div>\n                <div v-else class=\"row-current-value current-value-editable\">\n                    <input v-if=\"row.dataType.value === 'String' || row.dataType.value === 'Number'\"\n                        :type=\"row.dataType.value === 'String' ? 'text' : 'number'\" class=\"current-value-input\"\n                        :id=\"row.parameter\" :name=\"row.parameter\" v-model=\"row.currentValue\"\n                        @change=\"handleChanges(row)\" />\n                    <select v-if=\"row.dataType.value === 'Boolean'\" class=\"current-value-input\"\n                        v-model=\"row.currentValue\" @change=\"handleChanges(row)\">\n                        <option selected>false</option>\n                        <option>true</option>\n                    </select>\n                    <select v-if=\"row.dataType.value === 'Screen'\" class=\"current-value-input\"\n                        @change=\"handleScreen($event, row)\"\n                        :style=\"'color:' + (screens.some((screen) => screen.ID == row.currentValue) ? 'black' : '#A1A1A1')\">\n                        <option value=\"\" disabled :selected=\"!screens.some((screen) => screen.ID == row.currentValue)\">\n                            Select</option>\n                        <option v-for=\"screen in props.screens\" :id=\"screen.ID\"\n                            :selected=\"row.currentValue == screen.ID\" style=\"color: black !important\">\n                            {{ screen.name }}\n                        </option>\n                    </select>\n                    <select v-if=\"row.dataType.value === 'Http Method'\" class=\"current-value-input\"\n                        v-model=\"row.currentValue\"\n                        :style=\"'color:' + (restMethods.some((method) => method == row.currentValue) ? ' black' : '#A1A1A1')\"\n                        @change=\"handleChanges(row)\">\n                        <option value=\"\" disabled :selected=\"!restMethods.some((method) => method == row.currentValue)\">\n                            Select</option>\n                        <option v-for=\"restMethod in restMethods\" style=\"color: black !important\">{{ restMethod }}\n                        </option>\n                    </select>\n                </div>\n\n                <div class=\"row-default-value\">{{ row.defaultValue || \"-\" }}</div>\n                <div v-if=\"props.editable\">\n                    <i v-if=\"row.isDeletable\" class=\"mdi mdi-delete-outline row-operation\"\n                        @click=\"deleteVariable(row, index)\"></i>\n                    <i v-else class=\"mdi mdi-arrow-u-left-top row-operation\" @click=\"returnToDefaultValue(row)\"></i>\n                </div>\n            </div>\n        </div>\n    </div>\n</template>\n\n<script setup lang=\"ts\">\nimport { ProcessVariable } from \"@stechquick/algae/lib/qCloudTemp/process\";\nimport { ref, computed, Ref } from \"vue\";\nimport { ILocalization } from \"../../../../../domain/infrastructure/ILocalization\";\nimport { container } from \"../../../../../domain/core/diContainer\";\nimport { IModel } from \"../../../../../domain/model/models\";\nimport { ModifiedCallbackType } from \"../../../../../domain/model/editors/modifiedCallbackType\";\n\ninterface IProcessVariableProps {\n    editable?: boolean;\n    variables: Array<ProcessVariable>;\n    screens: Array<IModel>;\n    modified: ModifiedCallbackType;\n}\n\ninterface IProcessVariableRow {\n    parameter: ProcessVariable[\"key\"];\n    currentValue: ProcessVariable[\"value\"];\n    dataType: Ref;\n    defaultValue: ProcessVariable[\"value\"];\n    defaultDataType: ProcessVariable[\"key\"];\n    isDeletable: ProcessVariable[\"isDeletable\"];\n}\n\nconst props = defineProps<IProcessVariableProps>();\nconst localization = container.resolve<ILocalization>(ILocalization);\nconst dataTypes = [\"String\", \"Boolean\", \"Screen\", \"Number\", \"Http Method\"];\nconst restMethods = [\"GET\", \"POST\", \"PUT\", \"PATCH\", \"DELETE\", \"HEAD\", \"CONNECT\", \"OPTIONS\", \"TRACE\"];\n\nconst newVariableName = ref(\"\");\nconst variableAlreadyExists = ref(false);\n\nconst tableHeaders = ref([\"parameter\", \"dataType\", \"currentValue\", \"defaultValue\", \"operations\"]);\nconst tableRows = computed(() => props.variables.map<IProcessVariableRow>((variable) => {\n    return {\n        parameter: variable.key,\n        currentValue: variable.value,\n        defaultValue: variable.value,\n        dataType: ref(variable.type),\n        defaultDataType: variable.type,\n        isDeletable: variable.isDeletable,\n    } as IProcessVariableRow;\n}));\n\nfunction checkVariableNameExists() {\n    variableAlreadyExists.value = tableRows.value.find((row) => row.parameter == newVariableName.value) != undefined;\n}\n\nfunction addNewVariable() {\n    const variable: IProcessVariableRow = {\n        parameter: newVariableName.value,\n        currentValue: \"\",\n        dataType: ref(\"String\"),\n        defaultValue: \"\",\n        defaultDataType: \"\",\n        isDeletable: true,\n    };\n    if (variableAlreadyExists.value || newVariableName.value.length == 0) return;\n\n    tableRows.value.unshift(variable);\n    newVariableName.value = \"\";\n    handleChanges(variable);\n}\n\nfunction deleteVariable(row: IProcessVariableRow, index: number) {\n    tableRows.value.splice(index, 1);\n    handleChanges(row);\n}\n\nfunction returnToDefaultValue(row: IProcessVariableRow) {\n    row.currentValue = row.defaultValue;\n    row.dataType.value = row.defaultDataType;\n    handleChanges(row);\n}\n\nfunction handleChanges(row: IProcessVariableRow) {\n    let isFound = false;\n    props.variables.forEach(prop => {\n        if (prop.key === row.parameter) {\n            prop.type = row.dataType.value;\n            prop.value = row.currentValue;\n            isFound = true;\n        }\n    })\n    if (!isFound) {\n        props.variables.push({\n            key: row.parameter,\n            type: row.dataType.value,\n            value: row.currentValue,\n            isDeletable: row.isDeletable\n        } as ProcessVariable);\n    }\n\n    props.modified({ model: true });\n}\n\nfunction handleDataTypeChange(row: IProcessVariableRow) {\n    row.currentValue = \"\";\n    handleChanges(row)\n}\n\nfunction handleScreen($event: any, row: IProcessVariableRow) {\n    const selectedScreen = $event.target[$event.target.selectedIndex];\n    row.currentValue = selectedScreen.id;\n    handleChanges(row);\n}\n</script>\n\n<style scoped>\n.title {\n    position: absolute;\n    top: 118px;\n    left: 32px;\n    font-size: 24px;\n    font-family: \"Roboto\";\n    color: #212121;\n}\n\n.variable-count {\n   