\n    position: absolute;\n    top: 40px;\n    height: 528px;\n    width: 936px;\n    border: none;\n    overflow-x: hidden;\n    overflow-y: auto;\n}\n\n.table-body::-webkit-scrollbar {\n    width: 4px;\n}\n\n.table-body::-webkit-scrollbar-thumb {\n    background: #bcbcbc 0% 0% no-repeat padding-box;\n    border-radius: 10px;\n}\n\n.table-body::-webkit-scrollbar-thumb:hover {\n    background: #555;\n}\n\n.table-row {\n    width: 100%;\n    height: 44px;\n    display: flex;\n}\n\n.row-parameter {\n    margin-top: 14px;\n    margin-left: 12px;\n    font-size: 12px;\n    color: #212121;\n    font-family: \"Roboto\";\n    width: 140px;\n    display: inline-block;\n    white-space: nowrap;\n    overflow: hidden !important;\n    text-overflow: ellipsis;\n}\n\n.row-data-type {\n    margin-left: 41px;\n    font-size: 13px;\n    color: #212121;\n    font-family: \"Roboto\";\n    width: 154px;\n    display: inline-block;\n}\n\n.data-type {\n    margin-top: 12px;\n    white-space: nowrap;\n    overflow: hidden !important;\n    text-overflow: ellipsis;\n}\n\n.data-type-editable {\n    margin-top: 7px;\n}\n\n.data-type-combo {\n    width: 154px;\n    height: 30px;\n    background: #ffffff 0% 0% no-repeat padding-box;\n    border: 1px solid #a1a1a1;\n    border-radius: 4px;\n    padding-left: 6px;\n    font: normal normal normal 14px/19px Roboto;\n    color: #212121;\n}\n\n.current-value-input {\n    width: 220px;\n    height: 30px;\n    background: #ffffff 0% 0% no-repeat padding-box;\n    border: 1px solid #a1a1a1;\n    border-radius: 4px;\n    padding-left: 10px;\n    font: normal normal normal 14px/19px Roboto;\n    color: #212121;\n}\n\n.row-current-value {\n    margin-left: 48px;\n    font-size: 13px;\n    color: #212121;\n    font-family: \"Roboto\";\n    width: 220px;\n    display: inline-block;\n}\n\n.current-value {\n    margin-top: 12px;\n    white-space: nowrap;\n    overflow: hidden !important;\n    text-overflow: ellipsis;\n}\n\n.current-value-editable {\n    margin-top: 7px;\n}\n\n.row-default-value {\n    margin-left: 48px;\n    margin-top: 12px;\n    font-size: 13px;\n    color: #212121;\n    font-family: \"Roboto\";\n    width: 209px;\n    display: inline-block;\n    white-space: nowrap;\n    overflow: hidden !important;\n    text-overflow: ellipsis;\n}\n\n.row-operation {\n    margin-top: 11px;\n    margin-left: 12px;\n    font: normal normal normal 20px/25px Material Design Icons Desktop;\n    cursor: pointer;\n    display: inline-block;\n}\n</style>\n"],"sourceRoot":""}]);
