 from \"@stechquick/algae/lib/helpers/cryptoHelper\";\nimport { IProcessEditorUtil } from \"../../../../domain/useCase/IProcessEditorUtil\";\nimport { IModelsOfModule } from \"../../../../domain/presentation/editors/IProcessEditorModel\";\nimport { extractProcessVariables } from \"@stechquick/plateau-process-designer\";\nimport ProcessEditorHeader from \"./components/processEditorHeader.vue\";\nimport ProcessEditorFooter from \"./components/processEditorFooter.vue\";\nimport ScreenSelectionTab from \"./tabs/screenSelectionTab.vue\";\nimport ServiceCallConfigurationTab from \"./tabs/serviceCallConfigurationTab.vue\";\nimport AdvancedSettingsTab from \"./tabs/advancedSettings.vue\";\nimport ProcessVariablesTab from \"./tabs/processVariablesTab.vue\";\nimport SummaryTab from \"./tabs/summaryTab.vue\";\nimport { ProcessVariable } from \"@stechquick/algae/lib/qCloudTemp/process\";\nimport { IViewModel } from '../../../../domain/viewModel/IViewModel';\n\ninterface IProps {\n    functions: IProcessEditorProps[\"functions\"];\n    callbacks: IProcessEditorProps[\"callbacks\"];\n}\n\nconst executor = container.resolve<IUseCaseExecutor>(IUseCaseExecutor);\nconst studio = container.resolve<IStudio>(IStudio);\nconst processEditorUtil = container.resolve<IProcessEditorUtil>(IProcessEditorUtil);\nconst props = defineProps<IProps>();\nconst renderKey = ref(\"\");\nconst disableFinishButton = ref(false);\nconst title = ref(\"Process Wizard\");\nconst wizardHeight = ref(\"700px\");\n\nlet initialized = ref(false);\n\nconst processModel = ref(processEditorUtil.getEmptyProcessWizardModel(\"_\"));\nconst processEditorState = ref(processEditorUtil.getEmptyProcessEditorState());\nconst currentModule = ref({} as IModule);\nconst modelsOfModule = ref({\n    screens: [],\n    bpmns: [],\n} as IModelsOfModule);\nconst processVariablesEditable = ref(true);\nconst processVariables: Array<ProcessVariable> = [\n    {\n        key: \"selectApproverForm\",\n        type: \"String\",\n        value: \"\",\n        isDeletable: false,\n    },\n    {\n        key: \"supportingForm\",\n        type: \"String\",\n        value: \"/microui/modul_name/qjson/undefined.qjson\",\n        isDeletable: false,\n    },\n    {\n        key: \"isAutoAssign\",\n        type: \"String\",\n        value: \"\",\n        isDeletable: false,\n    },\n    {\n        key: \"resourceCode\",\n        type: \"String\",\n        value: \"\",\n        isDeletable: false,\n    },\n    {\n        key: \"useHierarchicalProcess\",\n        type: \"String\",\n        value: false,\n        isDeletable: false,\n    },\n    {\n        key: \"instUnitHierarchy\",\n        type: \"String\",\n        value: \"SAME\",\n        isDeletable: false,\n    },\n    {\n        key: \"ignoreLevelForDiffInstUnit\",\n        type: \"String\",\n        value: false,\n        isDeletable: false,\n    },\n    {\n        key: \"reviewForm\",\n        type: \"String\",\n        value: \"/microui/modul_name/qjson/undefined.qjson\",\n        isDeletable: false,\n    },\n    {\n        key: \"businessKey\",\n        type: \"Number\",\n        value: \"\",\n        isDeletable: false,\n    },\n    {\n        key: \"entityId\",\n        type: \"Screen\",\n        value: \"\",\n        isDeletable: false,\n    },\n    {\n        key: \"tenantId\",\n        type: \"Http Method\",\n        value: \"\",\n        isDeletable: false,\n    },\n    {\n        key: \"reviewStep\",\n        type: \"Boolean\",\n        value: \"\",\n        isDeletable: false,\n    },\n];\nprops.functions.getModel = async () => {\n    return { model: processModel.value, state: processEditorState.value };\n};\n\nprops.functions.setModel = async (model, module, state) => {\n    if (model.usingExistingBpmnFile && state.selectedExistingBpmn) {\n        title.value = state.selectedExistingBpmn.name;\n        wizardHeight.value = \"886px\";\n    } else {\n        title.value = \"Process Wizard\";\n        wizardHeight.value = \"700px\";\n    }\n    currentModule.value = module;\n    modelsOfModule.value = processEditorUtil.getModelsOfModule(module);\n    renderKey.value = CryptoHelper.CreateGuid();\n    processModel.value = model;\n    processEditorState.value = state;\n    initialized.value = true;\n};\n\nasync function handleNext() {\n    const stepper = processEditorState.value.stepper;\n    const selectedExistingBpmnFile = processModel.value.selectedExistingBpmnFile;\n    if (stepper.isTabValid) {\n        \n        stepper.currentStep++;\n        stepper.isTabValid = false;\n        props.callbacks.modified({ state: true });\n    } else { }\n    resizeWizard();\n    processVariablesEditable.value =\n        processModel.value.usingExistingBpmnFile && processEditorState.value.selectedExistingBpmn && stepper.currentStep == 1;\n}\n\nfunction handleBack() {\n    processEditorState.value.stepper!.currentStep--;\n    processEditorState.value.stepper!.isTabValid = false;\n    processVariablesEditable.value =\n        processModel.value.usingExistingBpmnFile &&\n        processEditorState.value.selectedExistingBpmn &&\n        processEditorState.value.stepper.currentStep == 1;\n    console.log(processVariablesEditable.value);\n    props.callbacks.modified({ state: true });\n    resizeWizard();\n}\n\nfunction handleSave() {\n    const viewModel = container.resolve<IViewModel>(IViewModel);\n    executor.execute(async () => {\n        await studio.save(viewModel.studio.currentItem!)\n    }, { loading: true });\n    handleFinish();\n}\n\nfunction handleDiscard() { }\nfunction handleFinish() {\n    const bpmnFile = processEditorUtil.generateBpmnFile(processModel.value, processEditorState.value.processFileName);\n    executor.execute(\n        async () => {\n            const createdItem = await executor.executeUseCase<ICreateModel>(ICreateModel, {\n                name: processEditorState.value.processFileName,\n                parentObjectId: currentModule.value.ID,\n                modelType: \"bpmn\",\n                modelBody: [{ key: \"bpmn\", model: bpmnFile }],\n                modelAdditionals: {},\n            });\n\n            processModel.value.selectedExistingBpmnFile.variables = processVariables;\n            processModel.value.processState = \"CREATE\";\n\n            processModel.value.selectedExistingBpmnFile.ID = createdItem.ID;\n            modelsOfModule.value.bpmns.push(createdItem);\n            disableFinishButton.value = true;\n            studio.openItem(createdItem, { fromHistory: false, noFocus: true });\n        },\n        { loading: true }\n    );\n    props.callbacks.modified({ state: true });\n}\n\nfunction resizeWizard() {\n    if (processModel.value.usingExistingBpmnFile && processEditorState.value.selectedExistingBpmn) {\n        wizardHeight.value = \"886px\";\n    } else if (processEditorState.value.stepper.currentStep == 4 || processEditorState.value.stepper.currentStep == 5) {\n        wizardHeight.value = \"886px\";\n    } else {\n        wizardHeight.value = \"700px\";\n    }\n}\n</script>\n<template>\n    <div class=\"process-editor\" :style=\"'height:' + wizardHeight\" v-if=\"initialized\" :key=\"renderKey\">\n        <ProcessEditorHeader :state=\"processEditorState\" :title=\"title\" />\n        <div v-if=\"processModel.usingExistingBpmnFile\">\n            <ProcessVariablesTab :editable=\"processVariablesEditable\"\n                :variables=\"processModel.selectedExistingBpmnFile.variables\" :screens=\"modelsOfModule.screens\"\n                :modified=\"props.callbacks.modified\" />\n        </div>\n        <div v-else>\n            <ScreenSelectionTab v-if=\"processEditorState.stepper.currentStep === 1\" :model=\"processModel\"\n                :models-of-module=\"modelsOfModule\" :state=\"processEditorState\" :modified=\"props.callbacks.modified\" />\n            <ServiceCallConfigurationTab v-if=\"processEditorState.stepper.currentStep === 2\" :model=\"processModel\"\n                :models-of-module=\"modelsOfModule\" :state=\"processEditorState\" :modified=\"props.callbacks.modified\" />\n            <AdvancedSettingsTab v-if=\"processEditorState.stepper.currentStep === 3\" :model=\"processModel\"\n                :modified=\"props.callbacks.modified\" />\n            <ProcessVariablesTab v-if=\"processEditorState.stepper.currentStep == 4\" :editable=\"true\"\n                :variables=\"processVariables\" :screens=\"modelsOfModule.screens\" :modified=\"props.callbacks.modified\" />\n            <SummaryTab v-if=\"processEditorState.stepper.currentStep === 5\" :model=\"processModel\"\n                :screens=\"modelsOfModule.screens\" :variables=\"processVariables\" />\n        </div>\n        <div></div>\n        <ProcessEditorFooter :handle-next=\"handleNext\" :handle-back=\"handleBack\" :handle-save=\"handleSave\"\n            :handle-discard=\"handleDiscard\" :state=\"processEditorState\" :model=\"processModel\" />\n    </div>\n    <!-- <div class=\"process-editor\" v-if=\"false\">\n        <ProcessDefinitionTab v-if=\"processEditorState.stepper.currentStep === 0\" :model=\"processModel\"\n            :models-of-module=\"modelsOfModule\" :state=\"processEditorState\" :modified=\"props.callbacks.modified\"\n            :enable-finish-button=\"() => (disableFinishButton = false)\" :is-next-active=\"isNextActive\"\n            :key=\"processDefinitionTabKey\" />\n        <EditProcessVariablesTab\n            v-if=\"processModel.processState == 'CREATE' && processEditorState.stepper.currentStep === 1\"\n            :model=\"processModel\" :models-of-module=\"modelsOfModule\" :state=\"processEditorState\"\n            :modified=\"props.callbacks.modified\" />\n        <SingleApprovalWizard v-if=\"\n            processModel.processType === SINGLE_APPROVAL.value &&\n            processEditorState.stepper.currentStep !== 0 &&\n            processModel.processState == 'CREATE'\n        \" :model=\"processModel\" :models-of-module=\"modelsOfModule\" :state=\"processEditorState\"\n            :modified=\"props.callbacks.modified\" />\n        <DoubleApprovalWizard v-if=\"\n            processModel.processType === DOUBLE_APPROVAL.value &&\n            processEditorState.stepper.currentStep !== 0 &&\n            processModel.processState == 'CREATE'\n        \" :model=\"processModel\" :models-of-module=\"modelsOfModule\" :state=\"processEditorState\"\n            :modified=\"props.callbacks.modified\" />\n        <TripleApprovalWizard v-if=\"\n            processModel.processType === TRIPLE_APPROVAL.value &&\n            processEditorState.stepper.currentStep !== 0 &&\n            processModel.processState == 'CREATE'\n        \" :model=\"processModel\" :models-of-module=\"modelsOfModule\" :state=\"processEditorState\"\n            :modified=\"props.callbacks.modified\" />\n        <QuadrupleApprovalWizard v-if=\"\n            processModel.processType === QUADRUPLE_APPROVAL.value &&\n            processEditorState.stepper.currentStep !== 0 &&\n            processModel.processState == 'CREATE'\n        \" :model=\"processModel\" :models-of-module=\"modelsOfModule\" :state=\"processEditorState\"\n            :modified=\"props.callbacks.modified\" />\n        <QuintupleApprovalWizard v-if=\"\n            processModel.processType === QUINTUPLE_APPROVAL.value &&\n            processEditorState.stepper.currentStep !== 0 &&\n            processModel.processState == 'CREATE'\n        \" :model=\"processModel\" :models-of-module=\"modelsOfModule\" :state=\"processEditorState\"\n            :modified=\"props.callbacks.modified\" />\n        <div>\n            <button style=\"margin-bottom: 5%\" :disabled=\"!processEditorState.stepper.currentStep\"\n                @click=\"handleBack\">Back</button>\n            <button v-if=\"\n                !processEditorState.stepper.isApproveTab && processModel.processState != 'CREATE' && processEditorState.stepper.currentStep === 1\n            \" @click=\"handleNext\" :disabled=\"!processEditorState.stepper.isTabValid\">\n                Next\n            </button>\n            <button v-if=\"processEditorState.stepper.isApproveTab\" @click=\"handleFinish\"\n                :disabled=\"disableFinishButton\">Finish</button>\n        </div>\n    </div> -->\n</template>\n<style scoped>\n.process-editor {\n    width: 1000px;\n    height: 700px;\n    background: white;\n    position: absolute;\n    margin-left: calc((100% - 1000px) / 2);\n    margin-top: 26px;\n    border: 1px solid #d5d5d5;\n    border-radius: 4px;\n    transition: height 250ms ease-in-out;\n}\n</style>\n"],"sourceRoot":""}]);
